$accent: #373AF5;
$secondary: #8083A4;
$base_strong: #2C2D76;
$base_weak: #FFFF;
$critic: #E5596D;

$basic_spacing: 8px;

$h1: 32px;
$h2: 24px;
$body: 16px;
$meta: 14px;

@mixin value-prefix($prop, $value) {
  @each $prefix in ('-webkit-', '-moz-', '') {
    #{$prop}: #{$prefix}#{$value};
  }
}
